import { Component, OnInit } from '@angular/core';

import { CreateService } from '../create.service';
import { Sign } from '../../../../shared';

@Component({
  selector: 'step2',
  template: require('./step2.component.html'),
  styles: [require('./step2.component.css')]
})
export class Step2Component implements OnInit {

  colors = ['#F5A623', '#97CC00', '#F8E71C', '#9013FE', '#58C9F3', '#FF6C60'];
  selectedColor = this.colors[0];
  sign: Sign;

  constructor(private _createService: CreateService) {}

  ngOnInit() {
    this.sign = this._createService.sign;
    this.sign.warnColor = this.selectedColor;
  }

  selectColor(color: string) {
    this.selectedColor = color;
    this.sign.warnColor = color;
  }

}


